import React, { useState, useEffect } from "react";
import { fetchMovies } from "../../../Config";
import Carousel from "react-bootstrap/Carousel";
import Button from "react-bootstrap/Button";
import { Link } from "react-router-dom";
import "./Landing.css";

export function MovieCarousel() {
  const [nowPlaying, setNowPlaying] = useState([]);

  useEffect(() => {
    const fetchAPI = async () => {
      setNowPlaying(await fetchMovies());
    };
    fetchAPI();
  }, []);

  function truncate(str, n) {
    return str?.length > n ? str.substr(0, n - 1) + "..." : str;
  }

  const movies = nowPlaying.slice(0, 5).map((item, index) => {
    return (
      <Carousel.Item style={{ maxHeight: "600px", width: "100%" }} key={index}>
        <img
          style={{ width: "100%", objectFit: "contain" }}
          src={item.backPoster}
          alt={item.title}
        />
        <Carousel.Caption>
          {" "}
          <h3 className="title">{item.title}</h3>
          <p className="overview">{truncate(item.overview, 150)}</p>
          <p>
            {item.release} | <span>{item.rating}/10</span>
          </p>
          <Button as={Link} to={`/movie/${item.id}`} variant="outline-primary">
            More
          </Button>
        </Carousel.Caption>
      </Carousel.Item>
    );
  });

  return (
    <div className="row mt-2">
      <div className="col">
        <Carousel
          slide={true}
          autoPlay={true}
          slidesshowspeed={5000}
          indicators={false}
        >
          {movies}
        </Carousel>
      </div>
    </div>
  );
}

export default MovieCarousel;
