import React, { useEffect, useState } from "react";
import { API_URL, API_KEY, IMAGE_BASE_URL, POSTER_SIZE } from "../../Config";
import GridCard from "../../commons/GridCards";
import MovieCarousel from "./Sections/MovieCarousel";
import MultiCarousel from "./Sections/MultiCarousel";
import "./Sections/Landing.css";
import Button from "react-bootstrap/Button";

export function LandingPage() {
  const [Movies, setMovies] = useState([]);
  const [MainMovieImage, setMainMovieImage] = useState(null);
  const [Loading, setLoading] = useState(true);
  const [CurrentPage, setCurrentPage] = useState(0);
  const [show, handleShow] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {
        handleShow(true);
      } else handleShow(false);
    });
    return () => {
      window.removeEventListener("scroll", LandingPage, true);
    };
  }, []);
  useEffect(() => {
    const endpoint = `${API_URL}movie/popular?api_key=${API_KEY}&language=en-US&page=1`;
    fetchMovies(endpoint);
  }, []);

  const fetchMovies = (endpoint) => {
    fetch(endpoint)
      .then((result) => result.json())
      .then((result) => {
        // console.log(result)
        // console.log('Movies',...Movies)
        // console.log('result',...result.results)
        setMovies([...Movies, ...result.results]);
        setMainMovieImage(MainMovieImage || result.results[0]);
        setCurrentPage(result.page);
      }, setLoading(false))
      .catch((error) => console.error("Error:", error));
  };

  const loadMoreItems = () => {
    let endpoint = "";
    setLoading(true);

    console.log("CurrentPage", CurrentPage);
    endpoint = `${API_URL}movie/popular?api_key=${API_KEY}&language=en-US&page=${
      CurrentPage + 1
    }`;

    fetchMovies(endpoint);
    // setTimeout(function () {
    //   window.scrollBy(0, window.innerHeight - 100);
    // }, 100);
  };

  return (
    <div className="container">
      <MovieCarousel />
      <MultiCarousel />

      <div className="container" style={{ textAlign: "center" }}>
        <h3>Movies by Latest</h3>
        <hr />
        <div className="row mt-3">
          {" "}
          {Movies &&
            Movies.map((movie, index) => (
              <React.Fragment key={index}>
                <GridCard
                  image={
                    movie.poster_path
                      ? `${IMAGE_BASE_URL}${POSTER_SIZE}${movie.poster_path}`
                      : null
                  }
                  movieId={movie.id}
                  movieName={movie.original_title}
                />
              </React.Fragment>
            ))}
        </div>

        {Loading && <div>Loading...</div>}

        <br />
        <div
          className={`${show && "add"}`}
          style={{
            display: "flex",
            justifyContent: "center",
            visibility: "hidden",

            width: "100%",
            margin: "auto",
          }}
        >
          <Button
            onClick={loadMoreItems}
            style={{ position: "fixed", bottom: "180px" }}
          >
            Load More
          </Button>
        </div>
      </div>
    </div>
  );
}

export default LandingPage;
